































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import listeners from '../listerners';

@Component
export default class MdController extends Vue {
  @Prop({ default: '' })
  private value: any;

  @Prop()
  private trueValue: any;
  @Prop({ default: false })
  private falseValue: any;
  @Prop()
  private defaultValue: any;

  // type
  @Prop({
    type: String,
    default: 'checkbox',
    validator: value => ['checkbox', 'radio'].includes(value)
  })
  private type: any;

  @Prop({ type: String })
  private name: any;
  @Prop({ type: Boolean })
  private disabled: any;
  @Prop({ type: Boolean })
  private required: any;
  @Prop({ type: Boolean })
  private readonly: any;
  @Prop({ type: Boolean })
  private checked: any;

  // variables
  private val: any = true;
  private id = Math.random()
    .toString()
    .split('.')[1];

  // getters
  private get box() {
    return this.$el.querySelector('.box') || undefined;
  }

  private get listenersController() {
    return {
      ...this.$listeners,
      input: (event: any) => {
        if (!this.readonly) {
          if (this.type !== 'radio') {
            if (typeof this.value === 'boolean') {
              this.$emit('input', event.target.checked);
            } else if (typeof this.value === 'object' && this.value !== null) {
              const array = this.value;
              const containValue = array.includes(this.defaultValue);

              if (!containValue) {
                array.push(this.defaultValue);
              } else {
                array.splice(array.indexOf(this.defaultValue), 1);
              }

              this.$emit('input', array);
            } else {
              if (this.trueValue) {
                if (this.trueValue !== this.value) {
                  this.$emit('input', this.trueValue);
                } else {
                  this.$emit('input', this.falseValue || null);
                }
              } else if (this.defaultValue !== this.value) {
                this.$emit('input', this.defaultValue);
              } else {
                this.$emit('input', this.falseValue || null);
              }
            }
          } else {
            this.$emit('input', this.defaultValue || null);
          }
        }
      }
    };
  }
  private get listeners() {
    return {
      ...this.$listeners,
      ...listeners(this)
    };
  }
  private get isChecked() {
    if (this.value) {
      if (this.type !== 'radio') {
        if (typeof this.value === 'boolean') {
          return this.value;
        } else if (typeof this.value === 'object' && this.value !== null) {
          const array = this.value;
          const containValue = array.includes(this.defaultValue);

          if (containValue) {
            return true;
          } else {
            return false;
          }
        } else {
          return this.value === this.defaultValue ||
            this.value === this.trueValue
            ? true
            : false;
        }
      } else {
        if (this.value === this.defaultValue) {
          return true;
        }
      }
    }

    return false;
  }

  private setChecked() {
    const input = this.$el.querySelector('input');

    if (input) {
      if (this.value) {
        if (this.type !== 'radio') {
          if (typeof this.value === 'boolean') {
            input.checked = this.value;
          } else if (typeof this.value === 'object' && this.value !== null) {
            const array = this.value;
            const containValue = array.includes(this.defaultValue);

            if (containValue) {
              input.checked = true;
            } else {
              input.checked = false;
            }

            this.$emit('input', array);
          } else {
            input.checked =
              this.value === this.defaultValue || this.value === this.trueValue
                ? true
                : false;
          }
        } else {
          if (this.value === this.defaultValue) {
            input.checked = true;
          } else {
            input.checked = false;
          }
        }
      } else {
        input.checked = false;
      }
    }
  }

  @Watch('value')
  private onChange() {
    this.setChecked();
  }

  @Watch('isChecked')
  private emitState() {
    const event = this.isChecked ? 'is-checked' : 'is-unchecked';
    this.$emit(event);
  }
}
