





















import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import listeners from '../listerners';

@Component
export default class MdInput extends Vue {
  @Prop()
  private value!: any;

  // ---
  @Prop({ type: String })
  private name: any;
  @Prop({ type: String, default: 'text' })
  private type: any;
  @Prop({ type: Boolean, default: false })
  private disabled: any;
  @Prop({ type: Boolean, default: false })
  private required: any;
  @Prop({ type: Number })
  private step: any;
  @Prop({ type: Boolean, default: false })
  private autofocus: any;
  @Prop({ type: String })
  private placeholder: any;
  @Prop({ type: Boolean })
  private readonly: any;
  @Prop({ type: Number })
  private min: any;
  @Prop({ type: Number })
  private max: any;
  @Prop({ type: Number })
  private minlength: any;
  @Prop({ type: Number })
  private maxlength: any;
  @Prop({ type: String })
  private accept: any;

  @Prop({ type: Function })
  private validator: any;

  @Prop({ type: Array, default: () => [] })
  private options!: Array<{ key: string; value: any; selected: boolean }>;

  @Prop({ default: () => Object() })
  private attrs!: {
    [key: string]: string | ((el: HTMLElement) => void);
  };

  // data
  private val: any = '';

  private get getType() {
    if (this.type === 'file') {
      return 'text';
    }
    return this.type;
  }

  public get listenersInput() {
    return {
      ...this.$listeners,
      blur: (event: any) => {
        this.$emit('blur', event);
      },
      change: (event: any) => {
        const op = this.options.filter(option => option.value === this.val);

        if (op.length) {
          this.$emit('input', op[0].value);
        }
      }
    };
  }

  @Watch('attrs', { deep: true })
  private initAttrs() {
    if (this.attrs) {
      for (const attr of Object.keys(this.attrs)) {
        const at = this.attrs[attr];

        if (typeof at === 'string') {
          const attrs: string[] = [];
          const old = this.$el.getAttribute(attr);

          if (old) {
            attrs.push(old);
          }

          attrs.push(at);
          this.$el.setAttribute('class', attrs.join(' '));
        } else {
          at(this.$el as HTMLElement);
        }
      }
    }
  }

  private mounted() {
    this.initAttrs();
    const op = this.options.filter(option => option.selected);

    if (op.length) {
      this.val = op[0].value;
      this.$emit('input', op[0].value);
    }
  }
}
