












import { Vue, Component, Prop } from 'vue-property-decorator';
import './style.scss';

@Component
export default class Collapse extends Vue {
  @Prop({ type: Boolean, default: false })
  private appear: any;

  @Prop({ type: Boolean, default: false })
  private allwaysOpen: any;

  // variables
  private uid = `md-collapse-${
    Math.random()
      .toString()
      .split('.')[1]
  }`;

  private openner(forced?: any) {
    this.$el.classList.toggle('open');

    setTimeout(() => {
      if (forced && typeof forced === 'boolean') {
        this.$el.classList.add('open');
      }

      const parent = this.$el.parentElement;

      if (parent) {
        const collapses = parent.querySelectorAll('.md-collapse');
        for (const col of collapses) {
          if (col.getAttribute('uid') !== this.uid) {
            col.classList.remove('open');
          }
        }
      }

      this.$emit(this.$el.classList.contains('open') ? 'open' : 'close');
    }, 0);
  }

  private mounted() {
    if (this.appear) {
      this.$el.classList.toggle('open');
    }
  }
}
