






import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import listeners from '../listerners';

@Component
export default class MdComponent extends Vue {
  @Prop({ default: 'div' })
  private tag!: string;

  @Prop({ default: () => Object() })
  private attrs!: {
    [key: string]: string | ((el: HTMLElement) => void);
  };

  @Watch('attrs', { deep: true })
  private init() {
    if (this.attrs) {
      for (const attr of Object.keys(this.attrs)) {
        const at = this.attrs[attr];

        if (typeof at === 'string') {
          const attrs: string[] = [];
          const old = this.$el.getAttribute(attr);

          if (old) {
            attrs.push(old);
          }

          attrs.push(at);
          this.$el.setAttribute('class', attrs.join(' '));
        } else {
          at(this.$el as HTMLElement);
        }
      }
    }
  }

  public get listeners() {
    return {
      ...this.$listeners,
      ...listeners(this)
    };
  }

  private mounted() {
    this.init();
  }
}
