
















































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import listeners from '../listerners';

@Component
export default class MdInput extends Vue {
  @Prop()
  private value: any;

  // ---
  @Prop({ type: String })
  private name: any;
  @Prop({ type: String, default: 'text' })
  private type: any;
  @Prop({ type: Boolean, default: false })
  private disabled: any;
  @Prop({ type: Boolean, default: false })
  private required: any;
  @Prop({ type: Number })
  private step: any;
  @Prop({ type: Boolean, default: false })
  private autofocus: any;
  @Prop({ type: String })
  private placeholder: any;

  @Prop({ type: Boolean })
  private placeholderIn!: boolean;

  @Prop({ type: Boolean })
  private readonly: any;
  @Prop({ type: Number })
  private min: any;
  @Prop({ type: Number })
  private max: any;
  @Prop({ type: Number })
  private minlength: any;
  @Prop({ type: Number })
  private maxlength: any;
  @Prop({ type: String })
  private accept: any;

  @Prop({ type: Function })
  private validator: any;

  @Prop({ default: () => Object() })
  private attrs!: {
    [key: string]: string | ((el: HTMLElement) => void);
  };

  // data
  private val: any = '';

  private get getType() {
    if (this.type === 'file') {
      return 'text';
    }
    return this.type;
  }

  private get listeners() {
    return {
      ...this.$listeners,
      ...listeners(this)
    };
  }
  public get listenersInput() {
    return {
      ...this.$listeners,
      blur: (event: any) => {
        this.$emit('blur', event);
      },
      input: (event: any) => {
        if (this.type !== 'file') {
          this.$emit('input', event.target.value);
        }
      },
      change: (event: any) => {
        this.$emit('change', event);
      }
    };
  }

  @Watch('value')
  private onChange() {
    if (this.type !== 'file') {
      if (this.val !== this.value) {
        this.val = this.value;
      }
    }
  }

  private uploaded() {
    const input = this.$refs.clooninput as HTMLElement;
    input?.click();
  }
  private change(e: any) {
    const files = e.target.files;
    if (this.type === 'file' && files.length) {
      const fr = new FileReader();
      fr.onload = () => {
        this.val = files[0].name;
        this.$emit('input', fr.result as string);
      };
      fr.readAsDataURL(files[0]);
    } else {
      this.val = this.value;
    }
  }

  @Watch('attrs', { deep: true })
  private initAttrs() {
    if (this.attrs) {
      for (const attr of Object.keys(this.attrs)) {
        const at = this.attrs[attr];

        if (typeof at === 'string') {
          const attrs: string[] = [];
          const old = this.$el.getAttribute(attr);

          if (old) {
            attrs.push(old);
          }

          attrs.push(at);
          this.$el.setAttribute('class', attrs.join(' '));
        } else {
          at(this.$el as HTMLElement);
        }
      }
    }
  }
  private init() {
    this.initAttrs();
    const input = this.$refs.input;
    if (input && this.validator) {
      this.validator(input);
    }
    this.onChange();
  }

  private mounted() {
    this.init();
  }
}
