























import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class MdModal extends Vue {
  @Prop()
  private mClass: any;

  @Prop({ type: Boolean })
  private closeOnEsc: any;

  @Prop({ type: Boolean, default: true })
  private closeOnBack: any;

  @Prop({ type: Boolean })
  private openOnMounted: any;

  private show = false;

  public get listeners() {
    return {
      ...this.$listeners,
      click: (event: any) => {
        this.$emit('click', event);
      },
      dblclick: (event: any) => {
        this.$emit('dblclick', event);
      },

      focus: (event: any) => {
        this.$emit('focus', event);
      },
      focusin: (event: any) => {
        this.$emit('focusin', event);
      },
      focusout: (event: any) => {
        this.$emit('focusout', event);
      },

      blur: (event: any) => {
        this.$emit('blur', event);
      },
      scroll: (event: any) => {
        this.$emit('scroll', event);
      },
      resize: (event: any) => {
        this.$emit('resize', event);
      },

      keyup: (event: any) => {
        this.$emit('keyup', event);
      },
      keydown: (event: any) => {
        this.$emit('keydown', event);
      },
      keypress: (event: any) => {
        this.$emit('keypress', event);
      },

      mouseenter: (event: any) => {
        this.$emit('mouseenter', event);
      },
      mousedown: (event: any) => {
        this.$emit('mousedown', event);
      },
      mousemove: (event: any) => {
        this.$emit('mousemove', event);
      },
      mouseout: (event: any) => {
        this.$emit('mouseout', event);
      },
      mouseup: (event: any) => {
        this.$emit('mouseup', event);
      },
      mousewheel: (event: any) => {
        this.$emit('mousewheel', event);
      },
      mouseleave: (event: any) => {
        this.$emit('mouseleave', event);
      },

      touchstart: (event: any) => {
        this.$emit('touchstart', event);
      },
      touchmove: (event: any) => {
        this.$emit('touchmove', event);
      },
      touchcancel: (event: any) => {
        this.$emit('touchcancel', event);
      },
      touchend: (event: any) => {
        this.$emit('touchend', event);
      }
    };
  }

  private preClose(e: any) {
    if (this.closeOnBack) {
      if (e.target.getAttribute('modal') === 'true') {
        this.close();
      }
    }
  }
  private open() {
    if (!this.show) {
      this.show = true;
      this.insertInBody();
    }
  }
  private close(): void {
    this.show = false;
    this.$emit('close');
    setTimeout(() => {
      this.insertInThis();
    }, 250);
  }

  private escKeydownTesting(e: any): void {
    if (e.keyCode === 27) {
      if (this.closeOnEsc) {
        this.close();
      }
      this.$emit('esc-keydown');
    }
  }

  private insertInBody() {
    const [body] = document.getElementsByTagName('body');
    body.insertBefore(this.$el, body.firstChild);

    setTimeout(() => {
      window.addEventListener('click', this.anOtherAreaClick);
      window.addEventListener('keydown', this.escKeydownTesting);
      this.$emit('open');
    }, 0);
  }
  private insertInThis() {
    const [parent] = document.getElementsByTagName('body');
    if (parent && this.$el && this.$el.parentNode === parent) {
      window.removeEventListener('keydown', this.escKeydownTesting);
      window.removeEventListener('click', this.anOtherAreaClick);
      parent.removeChild(this.$el);
    }

    setTimeout(() => {
      this.$emit('close');
    }, 0);
  }

  private anOtherAreaClick(e: any): void {
    this.$emit('other-area-clicked');
  }

  private beforeDestroy() {
    this.insertInThis();
  }
  private mounted() {
    if (this.openOnMounted) {
      this.open();
    }
  }
}
