
































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import listeners from '../listerners';
import './style.scss';

@Component
export default class MdImg extends Vue {
  // props
  @Prop({ type: Number })
  private maxWidth: any;
  @Prop({ type: Number })
  private minWidth: any;

  @Prop({ type: Boolean })
  private loader: any;

  @Prop({ type: String })
  private size: any;

  @Prop({ type: Number })
  private maxHeight: any;
  @Prop({ type: Number })
  private minHeight: any;

  @Prop()
  private value: any;

  @Prop({ type: Boolean })
  private required: any;

  @Prop({ type: String })
  private default: any;

  @Prop({ type: String })
  private src: any;

  @Prop({ type: String, default: 'div' })
  private tag: any;

  private image: string = '';
  private showUploader: boolean = false;

  private get img() {
    if (this.image) {
      return this.image;
    } else if (this.src) {
      return this.src;
    } else if (this.default) {
      return this.default;
    }
    return '';
  }

  private get listeners() {
    return {
      ...this.$listeners,
      ...listeners(this)
    };
  }

  private upload() {
    const input = this.$el.querySelector('input');

    if (input) {
      input.click();
    }
  }

  private change(e: any) {
    const files = e.target.files;
    if (files.length && /\.(jpg|jpeg|png)$/i.test(files[0].name)) {
      const fr = new FileReader();
      fr.onload = () => {
        const img = new Image();
        img.onload = () => {
          if (
            (this.maxWidth && this.maxWidth < img.width) ||
            (this.maxHeight && this.maxHeight < img.height) ||
            (this.minWidth && this.minWidth > img.width) ||
            (this.minHeight && this.minHeight > img.height)
          ) {
            alert('size incorrecte');
          } else {
            this.image = fr.result as string;
          }
        };
        img.src = fr.result as string;
      };
      fr.readAsDataURL(files[0]);
    }
  }

  @Watch('image')
  private emit() {
    this.$emit('input', this.image);
  }

  @Watch('value')
  private refresh() {
    if (this.value !== this.image) {
      this.image = this.value;
    }
  }

  private mounted() {
    this.refresh();
  }
}
